import React, { useState, useEffect } from 'react'
import './styles.css'

import ButtonRectangleOpacity from '../../config/components/buttonRectangleOpacity'
import ImageRounded from '../../config/components/imageRounded'
import OverMessage from '../../config/components/overMessage'
import MenuCard from '../../config/components/menuCard'

import imgComputer from '../../config/imgs/computer.jpg'
import reactLogoSvg from '../../config/svg/reactlogo.svg'
import reactLogo from '../../config/imgs/reactLogo.png'
import javaLogo from '../../config/imgs/javaLogo.png'
import springLogo from '../../config/imgs/springLogo.png'
import mysqlMariadbLogo from '../../config/imgs/mysqlMariaDBLogo.png'
import oracleLogo from '../../config/imgs/oracleLogo.png'
import githubLogo from '../../config/svg/github.svg'
import linkedinLogo from '../../config/svg/linkedin.svg'
import emailLogo from '../../config/svg/email.svg'
import webImg from '../../config/imgs/web.jpg'
import mobileImg from '../../config/imgs/mobile.jpg'
import pcImg from '../../config/imgs/pc.jpg'
import ImageGalleryModel from '../../config/components/imageGalleryModel'
import CardPhoto from '../../config/components/cardPhoto'

export default () => {
    const [selectedId, setSelectedId] = useState(1)

    const [overMessage, setOverMessage] = useState({
        show: false,
        message: '',
        type: ''
    })

    const [isShowWeb, setIsShowWeb] = useState(true)
    const [isShowMobile, setIsShowMobile] = useState(false)
    const [isShowExtra, setIsShowExtra] = useState(false)

    useEffect(() => {
        if (isShowWeb) {
            setIsShowMobile(false)
            setIsShowExtra(false)
        }
    }, [isShowWeb])


    useEffect(() => {
        if (isShowMobile) {
            setIsShowWeb(false)
            setIsShowExtra(false)
        }
    }, [isShowMobile])

    useEffect(() => {
        if (isShowExtra) {
            setIsShowWeb(false)
            setIsShowMobile(false)
        }
    }, [isShowExtra])

    return (
        <div>
            <OverMessage show={overMessage.show} message={overMessage.message} type={overMessage.type} setOverMessage={setOverMessage} />
            <div id="m_start" className="full homeBackground animatedParent whiteFont">
                <div className="full cloud">
                    <div className="full blackMask06 center mainTitle">
                        <div className="animated bounceIn text-center">
                            <h1>Luis Felipe</h1>
                            <h3>Web - Mobile </h3>
                            <div className="row center">
                                <a href="#m_stack">
                                    <ButtonRectangleOpacity label="INÍCIO" />
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div id="m_stack" className="full20 mainColorBk center">
                <div className="container whiteFont">
                    <div>
                        <div className="row">
                            <div className="col-12 col-sm-4 center">
                                <div className="row">
                                    <h4>Web</h4>
                                </div>
                                <div className="row">
                                    <p>
                                        A práticidade de acessar um link,
                                        de apertar f5 e ter a nova versão do sistema.
                                </p>
                                    {/* <p>
                                    O desenvolvimento WEB pode proporcionar diversas vantagens,
                                    como não deixar o usuário depêndente de uma máquina epecífica,
                                    o acesso ao sistema não precisar de instalações ou configurações etc.
                                    Para ter essas e outras vantagens uma boa ideia é trabalhar com uma arquitetura SOFEA “Service Oriented Front End Architecture”,
                                    a qual proporciona uma fácil divisão de responsábilidades no sistema,
                                    isso facilita no desenvolvimento, ajuda em um código mais limpo e ainda permite explorar
                                    recursos da máquina do usuário, diminuindo o processamento ocioso.
                                </p> */}
                                </div>
                            </div>
                            <div className="col-12 col-sm-4 center">
                                <div className="row">
                                    <h4>Stack</h4>
                                </div>
                                <div className="row">
                                    <div className="col-12 col-sm-4">
                                        <img src={reactLogoSvg} className="rotateElement" alt="React Logo" />
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 col-sm-4 center">
                                <div className="row">
                                    <h4>Mobile</h4>
                                </div>
                                <div className="row">
                                    <p>
                                        É mais fácil esquecer a carteira do que o celular
                                </p>
                                    <p>

                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="full container">
                <div className="row padding30">
                    <h3>Projetos</h3>
                </div>
                <div className="row">
                    <div className="col-12 centerRow">
                        <MenuCard title="Web" description="Click para ver projetos WEB" img={webImg} isShow={isShowWeb} setIsShow={setIsShowWeb} />
                        <MenuCard title="Mobile" description="Click para ver projetos WEB" img={mobileImg} isShow={isShowMobile} setIsShow={setIsShowMobile} />
                        <MenuCard title="Extra" description="Click para ver projetos WEB" img={pcImg} isShow={isShowExtra} setIsShow={setIsShowExtra} />
                    </div>
                    <div className="col-12">
                        <div>
                            <div className="row">
                                <h3><span>Projetos</span></h3>
                            </div>
                            <div className="row">
                                <p>
                                    Análisar, projetar e implementar soluções que realmente façam a diferença.
                                    </p>
                            </div>
                            {
                                isShowWeb &&
                                <div className="row">
                                    <div className="col-12">
                                        <div className="row">
                                            <div className="col-12">
                                                <div className="row">
                                                    <div className="col-12 col-sm-4 padding30">
                                                        <img src={webImg} className="imgProjects delay" />
                                                    </div>
                                                    <div className="col-12 col-sm-8">
                                                        <div className="row">
                                                            <ul>
                                                                <li>
                                                                    <h6>
                                                                        <span>Resumo:</span>
                                                                    </h6>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                        <div className="row">
                                                            <div className="row">
                                                                {/* <h6>Integração entre setores</h6> */}
                                                                {/* <p>
                                                                    Esse projeto tinha o objetivo de controlar o fluxo de produção,
                                                                    partindo da entrada do pedido até a fabricação do produto,
                                                                    passando por alguns setores da empresa até chegar no estágio de produção.
                                                                </p> */}
                                                                <p>Integração entre setores: Da entrada do pedido até a produção;</p>
                                                                <p>Reserva de almoço: Utilizando código de barras no crachá - para evitar o desperdício;</p>
                                                                <p>Logs de alteração: Controlar todas as alterações do sistema;</p>
                                                                <p>Monitoramento de exceções: Mapeamento dos erros gerados em um sistema, gerando um relatorio com as inconsitêcias que podem gerar um erro;</p>
                                                                {/* <p>Coletor de dados: Aplicativo para setor de expedição controlar as entregas - integração com ERP;</p> */}
                                                                <p>Controle de estoque: Entradas e saídas controlando material por setor - Agendar entradas - Alerta de estoque baixo;</p>
                                                            </div>
                                                            <div className="row">
                                                                <ul>
                                                                    <li>
                                                                        <h6>
                                                                            <span>Detalhes Técnicos:</span>
                                                                        </h6>
                                                                    </li>
                                                                </ul>
                                                            </div>
                                                            <div className="row padding30">

                                                                <ul>
                                                                    <li>ReactJS</li>
                                                                    <li>Java
                                                                        <ul>
                                                                            <li>JPA</li>
                                                                            <li>Spring</li>
                                                                            <li>JDBC</li>
                                                                            <li>Design Pattern</li>
                                                                            <li>Generics</li>
                                                                        </ul>
                                                                    </li>
                                                                    <li>Banco de dados
                                                                        <ul>
                                                                            <li>MySQL</li>
                                                                            <li>Oracle</li>
                                                                            <li>MongoDB</li>
                                                                        </ul>
                                                                    </li>
                                                                </ul>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            }

                            {
                                isShowMobile &&
                                <div className="row">
                                    <div className="col-12">
                                        <div className="row">
                                            <div className="col-12">
                                                <div className="row">
                                                    <div className="col-12 col-sm-4 padding30">
                                                        <img src={mobileImg} className="imgProjects delay" />
                                                    </div>
                                                    <div className="col-12 col-sm-8">
                                                        <div className="row">
                                                            <ul>
                                                                <li>
                                                                    <h6>
                                                                        <span>Resumo:</span>
                                                                    </h6>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                        <div className="row">
                                                            <div className="row">
                                                                <p>Coletor de dados: Aplicativo para setor de expedição controlar as entregas - integração com ERP;</p>
                                                            </div>
                                                            <div className="row">
                                                                <ul>
                                                                    <li>
                                                                        <h6>
                                                                            <span>Detalhes Técnicos:</span>
                                                                        </h6>
                                                                    </li>
                                                                </ul>
                                                            </div>
                                                            <div className="row padding30">
                                                                <ul>
                                                                    <li>React Native</li>
                                                                    <li>LSP - Linguagem Senior de Programação</li>
                                                                    <li>Java
                                                                        <ul>
                                                                            <li>JPA</li>
                                                                            <li>Spring</li>
                                                                            <li>Design Pattern</li>
                                                                        </ul>
                                                                    </li>
                                                                    <li>Banco de dados
                                                                        <ul>
                                                                            <li>MySQL</li>
                                                                            <li>Oracle</li>
                                                                        </ul>
                                                                    </li>
                                                                </ul>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            }

                            {
                                isShowExtra &&
                                <div className="row">
                                    <div className="col-12">
                                        <div className="row">
                                            <div className="col-12">
                                                <div className="row">
                                                    <div className="col-12 col-sm-4 padding30">
                                                        <img src={pcImg} className="imgProjects delay" />
                                                    </div>
                                                    <div className="col-12 col-sm-8">
                                                        <div className="row">
                                                            <ul>
                                                                <li>
                                                                    <h6>
                                                                        <span>Resumo:</span>
                                                                    </h6>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                        <div className="row">
                                                            <div className="row padding30">
                                                                <div className="col-12">
                                                                    <p>TCC - Sistema de Semáforo automatizado com visão computacional;</p>
                                                                    <p>APIs;</p>
                                                                    <p>Integrações;</p>
                                                                    <p>Documentação de requisitos;</p>
                                                                    <p>Definição de padrões de desenvolvimento;</p>
                                                                </div>
                                                            </div>
                                                            <div className="row padding30">
                                                                <div className="col-12 padding30">
                                                                    <ul>
                                                                        <li>
                                                                            <h6>
                                                                                <span>Detalhes Técnicos:</span>
                                                                            </h6>
                                                                        </li>
                                                                    </ul>
                                                                </div>
                                                            </div>
                                                            <div className="row padding30">
                                                                <ul>
                                                                    <li>Java
                                                                        <ul>
                                                                            <li>JPA</li>
                                                                            <li>Spring</li>
                                                                            <li>JDBC</li>
                                                                            <li>Design Pattern</li>
                                                                            <li>Generics</li>
                                                                            <li>Python</li>
                                                                        </ul>
                                                                    </li>
                                                                    <li>Banco de dados
                                                                        <ul>
                                                                            <li>MySQL</li>
                                                                            <li>Oracle</li>
                                                                            <li>MongoDB</li>
                                                                        </ul>
                                                                    </li>
                                                                </ul>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div className="full40 bannerBackground">
                <div className="full40 blackMask06 center">
                    <div className="row">
                        <img src={reactLogo} className="wh100 delay" alt="React Logo" />
                        <img src={javaLogo} className="wh100 delay" alt="React Logo" />
                        <img src={springLogo} className="wh100 delay" alt="React Logo" />
                        <img src={mysqlMariadbLogo} className="wh100 delay" alt="React Logo" />
                        <img src={oracleLogo} className="wh100 delay" alt="React Logo" />
                    </div>
                </div>
            </div>
            <div className="full container">
                <div className="row padding30">
                    <h3>Componentes</h3>
                </div>
                <div className="row">
                    <div className="col-6 col-md-4 col-lg-3">
                        <ImageGalleryModel id="img1" img={pcImg} />
                    </div>
                    <div className="col-6 col-md-4 col-lg-3">
                        <ImageGalleryModel id="img2" img={webImg} />
                    </div>
                    <div className="col-6 col-md-4 col-lg-3">
                        <ImageGalleryModel id="img3" img={mobileImg} />
                    </div>
                </div>
                <div className="row padding30 center">
                    <CardPhoto backgroundImage={pcImg} title="Components" text="Esses componentes foram desenvolvidos utilizando ReactJS, buscando reutilização e padrões com componentização." />
                </div>
            </div>
            <div className="full contactBackground">
                <div className="full blackMask06x">

                </div>
            </div>
            <div className="full60">
                <div className="full60 blackbackground center whiteFont">
                    <div className="container row center">
                        <div className="row">
                            <div className="col-6 col-sm-4 center">
                                <a href="https://github.com/7luisfelipe" target="_blank">
                                    <img src={githubLogo} className="wh50 delay" alt="React Logo" />
                                </a>
                            </div>
                            <div className="col-6 col-sm-4 center">
                                <a href="https://www.linkedin.com/in/luis-felipe-silva-130b72171/" target="_blank">
                                    <img src={linkedinLogo} className="wh50 delay" alt="React Logo" />
                                </a>
                            </div>
                            <div className="col-6 col-sm-4 center" onClick={() => {
                                setOverMessage({
                                    show: !overMessage.show,
                                    message: 'luis14_felipe@hotmail.com',
                                    type: ''
                                })
                            }}
                            >
                                <img src={emailLogo} className="wh50 delay" alt="React Logo" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <p>

                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    )
}