import React, { Component } from 'react'
import './styles.css'

export default class CardPhoto extends Component {

    render() {
        return (
            <div className="row cardWithPhoto">
                <div className="col-6 cardPhotoBackground" style={{ backgroundImage: `url(${this.props.backgroundImage})` }}>
                    <div className="">

                    </div>
                </div>
                <div className="col-6 text-center padding100Top center">
                    <h3>{this.props.title}</h3>
                    <p>
                        {this.props.text}
                    </p>
                </div>
            </div>
        )
    }
}