import React, { useEffect } from 'react'
import './styles.css'

export default ({ title, description, img, isShow, setIsShow }) => {
    useEffect(() => {
        if (isShow) {
            document.getElementById(title + '_id').classList.add('cardMenuBoxSelected')
        } else {
            document.getElementById(title + '_id').classList.remove('cardMenuBoxSelected')
        }
    }, [isShow])
    return (
        <div id={title + '_id'} className="cardMenuBox delay3 margin10" onClick={() => setIsShow(!isShow)}>
            <div className="row">
                <img src={img} className="imgMenuCard" />
            </div>
            <div className="row center">
                {title}
            </div>
        </div>
    )
}