import React from 'react'
import './styles.css'

export default ({ show, message, type, setOverMessage }) => {
    const handleClick = () => {
        console.log('...handleClick...')
        setOverMessage({
            show: false,
            message: '',
            type: ''
        })
    }
    return (
        <div>
            {
                show ?
                    (
                        <div className={"overMessageBox animate__animated animate__fadeInDown center boxShadow text-center " + (type == "success" ? "successBk" : type == "error" ? "errorBk" : "opacityBK")}
                            onClick={() => {
                                handleClick()
                            }}
                        >
                            <h6>{message}</h6>
                        </div>
                    )
                    :
                    null
            }
        </div>
    )
}