import React from 'react';

function ButtonRectangleOpacity(props) {
    return (
        <div>
            <button type="button" className="btn btn-primary padding10 padding50Side opacitybk03 animated bounceIn">
                <div>
                    {props.label}
                </div>
            </button>
        </div>

    );
}

export default ButtonRectangleOpacity;