import React from 'react';
import './styles.css'

let isShowing = false

function showImg(elementId) {
    try {
        if (!isShowing) {
            document.getElementById('img' + elementId).classList.add('fullImage2')
            document.getElementById('img' + elementId).classList.remove('zoom2')
            document.getElementById('imgBox' + elementId).classList.add('imgModel')
            document.getElementById('imgBoxInside' + elementId).classList.add('imgModelInside')
            document.getElementById('imgBox' + elementId).style.position = 'fixed'
            isShowing = true
        } else {
            document.getElementById('img' + elementId).classList.remove('fullImage2')
            document.getElementById('img' + elementId).classList.add('zoom2')
            document.getElementById('imgBox' + elementId).classList.remove('imgModel')
            document.getElementById('imgBoxInside' + elementId).classList.remove('imgModelInside')
            document.getElementById('imgBox' + elementId).style.position = 'relative'
            isShowing = false
        }
    } catch (err) {
        console.log('e...logout')
        console.log(err)
    }
}

function ImageGalleryModel(props) {
    return (
        <div className="col-12" id={`imgBox${props.id}`}>
            <div id={`imgBoxInside${props.id}`}>
                <img id={`img${props.id}`} onClick={e => showImg(props.id)} src={props.img} class="imageGallery2 zoom2 img-fluid top40 delay" alt=""></img>
            </div>
        </div>
    )
}

export default ImageGalleryModel;
